* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

section {
  margin: 1em 0 1em 0;
  max-width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 1em;
  flex: 3;
  flex-direction: row;
}

article {
  flex-basis: 26%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* min-height: fit-content; */
  size: fit-content;
  background-color: gray;
  padding: 0.5em;
  border: 0.1em solid gray;
  border-radius: 0.3em;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  /* background-color: gray; */
  /* opacity: 0.5; */
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.popup-wrapper {
  background-color: gray;
  padding: 1em;
  border: 0.5em solid gray;
  border-radius: 1em;
}

img {
  width: 18em;
  height: 28em;
}

.search {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1em;
}
